@import url(libs/slick.css);
@font-face {
  font-family: "Gothic";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/CenturyGothic.eot");
  src: url("../fonts/CenturyGothic.eot?#iefix") format("embedded-opentype"), url("../fonts/CenturyGothic.woff") format("woff"), url("../fonts/CenturyGothic.ttf") format("truetype");
}

@font-face {
  font-family: "Gothic-Bold";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/CenturyGothic-Bold.eot");
  src: url("../fonts/CenturyGothic-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/CenturyGothic-Bold.woff") format("woff"), url("../fonts/CenturyGothic-Bold.ttf") format("truetype");
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Gothic';
  font-size: 14px;
  line-height: 1.85;
}

.title-lg {
  font-size: 48px;
  color: #ffffff;
  font-family: 'Gothic-Bold';
  line-height: 1.23;
}

.title-md {
  font-size: 36px;
  color: #003820;
  position: relative;
  display: inline-block;
}

.title-md:after {
  content: '';
  position: absolute;
  width: 120%;
  height: 30px;
  background: #6CBE45;
  bottom: 3px;
  left: -10%;
  right: -10%;
  z-index: -1;
}

.green {
  color: #7EC65C;
  font-family: 'Gothic-Bold';
}

.bold {
  font-family: 'Gothic-Bold';
}

.btn-white {
  font-size: 18px;
  padding: 10px;
  border-radius: 40px;
  border: 1px solid #E4F3DD;
  box-shadow: 0px 10px 10px rgba(0, 56, 32, 0.1);
  color: #E4F3DD;
  text-decoration: none;
  text-align: center;
  display: block;
}

.header {
  position: fixed;
  z-index: 10;
  width: 100%;
}

.header__in {
  background: rgba(112, 182, 44, 0.9);
  font-family: 'Gothic-Bold';
}

.container {
  max-width: 1280px;
  padding: 0 50px;
  margin: 0 auto;
  position: relative;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

.tel {
  padding: 13px 30px;
  background: linear-gradient(360deg, #003820 0%, #56A33E 120%);
  opacity: 0.8;
  border-radius: 33px;
}

.tel__link {
  display: block;
  text-decoration: none;
  color: #E4F3DD;
}

.menu {
  display: flex;
}

.menu__item {
  margin-right: 15px;
}

.menu__link {
  font-family: 'Gothic-Bold';
  color: #ffffff;
  text-decoration: none;
}

.lang {
  display: flex;
  flex-direction: column;
}

.lang__item {
  display: block;
  color: #FFFFFF;
  font-size: 12px;
  line-height: 1.25;
  text-decoration: none;
  color: #FFFFFF;
}

.lang__item:not(:last-child) {
  margin-bottom: 7px;
}

.logo-circle {
  position: absolute;
  right: 50px;
  top: 20px;
  animation-name: bounce;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
  animation-delay: 3s;
}

@keyframes bounce {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.social {
  position: absolute;
  right: 0;
  top: 160px;
  text-align: right;
  display: inline-block;
}

.social-icons {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  right: 0;
}

.one {
  position: relative;
  height: 640px;
  overflow: hidden;
  padding-top: 77px;
}

.slider-one__info {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url("../img/Frame.svg");
  background-repeat: no-repeat;
  background-position: 6%;
  background-size: cover;
}

.slider-one__info-in {
  padding-top: 200px;
  max-width: 496px;
}

.slider-one__text {
  margin: 40px 0;
  font-size: 18px;
  color: #E4F3DD;
  max-width: 300px;
  line-height: 1.61;
}

.slider-one__item img {
  display: block;
  width: 100%;
  object-fit: cover;
  height: 640px;
}

.slider-one__btns {
  display: flex;
  cursor: pointer;
}

.slider-one__btns img {
  display: block;
}

.slider-one__btn {
  width: 120px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-one__btn-prev {
  transition: background-image 2s;
  background-image: none;
}

.slider-one__btn-prev:hover {
  background-image: url("../img/Vector.svg");
}

.slider-one__btn-next {
  transform: rotate(-180deg);
}

.slider-one__btn-next:hover {
  background-image: url("../img/Vector.svg");
}

.two {
  padding: 40px 0 70px;
}

.two__title {
  text-align: center;
  margin-bottom: 70px;
}

.two__wrap {
  display: flex;
  justify-content: space-between;
  margin: 0 -10px;
}

.two__item {
  width: 50%;
  margin: 0 10px;
}

.two__subtitle {
  margin-bottom: 32px;
  color: #003820;
  font-family: 'Gothic-Bold';
  font-size: 24px;
  line-height: 1.6;
}

.two__info {
  color: #567668;
  overflow-y: scroll;
  height: 200px;
}

.two__info::-webkit-scrollbar {
  width: 3px;
}

.two__info::-webkit-scrollbar-thumb {
  background-color: #6CBE45;
}

.slider-two__item {
  margin: 0 15px;
}

.slider-two__top {
  margin-bottom: 30px;
  position: relative;
}

.slider-two__title {
  color: #003820;
  font-family: 'Gothic-Bold';
  font-size: 24px;
  line-height: 1.6;
  text-align: center;
}

.slider-two__item {
  height: 234px;
}

.slider-two__item img {
  display: block;
  height: 100%;
  max-width: 100%;
  object-fit: cover;
}

.slider-two__btns {
  position: absolute;
  top: -5px;
  display: flex;
  justify-content: space-between;
  left: 20px;
  right: 20px;
}

.slider-two__btn {
  width: 66px;
  height: 66px;
  background-color: white;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  transition: all 0.5s;
}

.slider-two__btn:hover {
  background-color: #6CBE45;
}

.slider-two__btn svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-two__btn-next {
  transform: rotate(180deg);
}

.three {
  background: url("../img/text .jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 90px 0 60px;
}

.three__title {
  margin-bottom: 35px;
}

.three__info {
  display: flex;
  justify-content: space-between;
  margin: 0 -40px;
}

.three__item {
  width: 50%;
  margin: 0 40px;
}

.three__btn {
  text-align: center;
  margin: 0 auto;
  max-width: 350px;
  width: 100%;
}

.list-block__text {
  padding-left: 40px;
  color: #E4F3DD;
}

.list-block__item {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.list-block__item img {
  width: 43px;
  height: 43px;
}

.four {
  padding-top: 40px;
}

.four__title {
  text-align: center;
}

.four__top {
  padding: 40px 0;
  background: url("../img/rectangle.jpg");
}
